\n        },\n\n        beforeDestroy: function () {\n            this.rights = null\n        },\n\n        methods: {\n            submitForm() {\n                this.$refs.TaskForm.validate((valid) => {\n                    if (valid) {\n                        alert('submit!');\n                      this.axios\n                        .post('http://localhost:3000/test', {\n                          data: this.TaskForm,\n                        })\n                        .catch(error => {\n                          console.log(error)\n                          this.errored = true\n                        })\n                        .finally(() => this.loading = false)\n                    } else {\n                        console.log('error submit!!');\n                        return false;\n                    }\n                });\n            },\n\n            resetForm() {\n                this.$refs.TaskForm.resetFields();\n            },\n\n            handleChange(value) {\n                console.log(value)\n            },\n\n            onEditorChange({ quill, html, text }) {\n                console.log('editor change!', quill, html, text)\n                this.content = html\n            },\n\n            // 监听穿梭框组件添加\n            add(data){\n                console.log(data)\n            },\n            // 监听穿梭框组件移除\n            remove(data){\n                console.log(data)\n            }\n        }\n    }\n</script>\n\n<style scoped>\n\n    .clear{\n        clear: both;\n    }\n\n    #counter{\n        clear: both;\n        float: left;\n    }\n</style>"],"sourceRoot":""}]);

